<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">我的预约</block>
		</cu-custom>

		<view class="cu-timeline" v-if="recordItem.length>0">
			<template v-for="(item,index) in recordItem">

				<view class="cu-time">{{item.createdAt|formatDate}}</view>
				<view class="cu-item text-red">

					<view class="bg-orange content">
						<text>你预约了自习室:{{item.roomName}},座位号{{item.roomSeatName}}</text>
						<text style='padding-left: 20upx;'>预约时间:{{item.appointTime|formatDateTime}}</text>
						<text style='padding-left: 20upx;'>备注:{{item.content}} </text>
						<text style='padding-left: 20upx;' v-if='item.status==0||item.status==null'>状态:待入座 </text>
						<text style='padding-left: 20upx;' v-if='item.status==1'>状态:预约成功 </text>
						<text style='padding-left: 20upx;' v-if='item.status==2'>状态:预约失败 </text>
							<text style='padding-left: 20upx;' v-if='item.status==3'>状态:违约 </text>	
					</view>
					<view><button class="cu-btn block theme-bg-color margin-tb-sm lg"  v-if='item.status==0||item.status==null' @click="Cancel(item)">你要取消预约吗</button>
</view>
				</view>


			</template>
		</view>
		<view v-else>
			<cu-no-data></cu-no-data>
		</view>

		<view style="height:120upx"></view>
		<!-- 	<view class="publish" @click="Publish">
			<text class="lg text-gray cuIcon-edit" style="color:white;font-size: 60upx;"></text>
		</view> -->

	
	</view>
</template>

<script>
	import store from "@/store";
	import {
		GetFormatShortDate,
		GetFormatFullDate
	} from '@/utils/comm.js'
	import {
		mapGetters
	} from "vuex";
	var startDate = new Date();
	let year = startDate.getFullYear();
	let month = startDate.getMonth() + 1;
	let day = startDate.getDate();
	var beginDate = `${year}-${month}-1`;

	var endDate = `${year}-${month}-${day}`;



	export default {
		components: {

		},
		data() {
			return {
				isCard: false,
				TabCur: 0,
				beginDate: beginDate,
				endDate: endDate,
				recordItem: [],
				keyword: ""
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		async onShow() {

			if (this.$comm.CheckToken()) {
				await this.GetMyListAsyncApi();
			}
		},
		methods: {

			async StartDateChange({
				detail: {
					value
				}
			}) {
				this.beginDate = value;
				this.GetMyListAsyncApi();
			},

			async EndDateChange({
				detail: {
					value
				}
			}) {
				this.endDate = value;
				this.GetMyListAsyncApi();
			},
			async Cancel(item){
				var that=this;
				uni.showModal({
					content:"你要取消预约吗",
					success:async function (res) {
						if(res.confirm)
						{
							let url = `/make/an/appoint/delete?id=${item.id}`;
						 	await that.$http.PostAsync(url,{});
							that.GetMyListAsyncApi();
						}
					}
				})
			},
			async GetMyListAsyncApi() {
				let url = "/make/an/appoint/findByModal";

				this.$comm.ShowLoading();
				let {
					success,
					message,
					data
				} = await this.$http.PostAsync(url, {
					beginDate: this.beginDate,
					endDate: this.endDate,

				});
				
				if (success == true) {
					this.recordItem = data.list;
					console.log("dd",this.recordItem,success);
				} else {
					this.$comm.ShowToast(message);
				}
				uni.hideLoading()
			},


			async ToDetail(id) {
				if (this.$comm.CheckToken()) {

					uni.navigateTo({
						url: `/pages/exercise/exercise-detail?id=${id}`
					})

				}
			},

		}
	}
</script>

